<template>
  <el-container class="content">
    <el-main>
       <div class="timeSearch" >
         <el-label>时间范围：</el-label>
          <el-date-picker
            v-model="dateTimes"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptionsx"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            size="mini"
          ></el-date-picker>
        </div>
        <div class="el-main-conent" >
          <div class="area-main-conent">
            <el-header>
              <HeaderTitle class="header-title" :titleName="'维修'">
                <div slot="head-operation" class="head-operation">
                  <HeadOperationItem name="showInfo" @onOprationItem="exportTable('repairTable')">
                    <i slot="operation-icon" class="icon-export"></i>
                    <span slot="operation-name">{{BTN_TEXT.EXPORT}}</span>
                  </HeadOperationItem>
                </div>
              </HeaderTitle>
            </el-header>
            <div class="area-main-conent">
              <vxe-grid
                border="full"
                resizable
                keep-source
                ref="repairTable"
                class="vxe-table-element"
                height="300"
                :columns="tableColumnsRepair"
                :data="tableDatasRepair"
              ></vxe-grid>
            </div>
          </div>
          <div class="area-main-conent">
            <el-header>
              <HeaderTitle class="header-title" :titleName="'保养'">
                <div slot="head-operation" class="head-operation">
                  <HeadOperationItem name="showInfo" @onOprationItem="exportTable('maintainTable')">
                    <i slot="operation-icon" class="icon-export"></i>
                    <span slot="operation-name">{{BTN_TEXT.EXPORT}}</span>
                  </HeadOperationItem>
                </div>
              </HeaderTitle>
            </el-header>
            <div class="area-main-conent">
              <vxe-grid
                border="full"
                resizable
                keep-source
                ref="maintainTable"
                class="vxe-table-element"
                height="300"
                :columns="tableColumnsMaintain"
                :data="tableDatasMaintain"
              ></vxe-grid>
            </div>
          </div>
          <div class="area-main-conent">
            <el-header>
              <HeaderTitle class="header-title" :titleName="'点检'">
                <div slot="head-operation" class="head-operation">
                  <HeadOperationItem name="showInfo" @onOprationItem="exportTable('checkTable')">
                    <i slot="operation-icon" class="icon-export"></i>
                    <span slot="operation-name">{{BTN_TEXT.EXPORT}}</span>
                  </HeadOperationItem>
                </div>
              </HeaderTitle>
            </el-header>
            <div class="area-main-conent">
              <vxe-grid
                border="full"
                resizable
                keep-source
                ref="checkTable"
                class="vxe-table-element"
                height="300"
                :columns="tableColumnsCheck"
                :data="tableDatasCheck"
              ></vxe-grid>
            </div>
          </div>
        </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/DeviceConfiguration/deviceUphold';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/DeviceConfiguration/deviceUphold.less';
</style>
