<template>
  <div>
    <div v-if="!showExtend">
      <el-container class="content">
        <el-header>
          <header-buttons
          @buttonClickEvent="buttonClickEvent"
          :cssType="cssType"
          showBack=true
          showTitle=true
          :titleName="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_INFO"
          >
          </header-buttons>
          <!-- <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_INFO">
            <div slot="head-operation" class="head-operation">
              <el-button type="primary" size="mini" @click="goBack">{{BTN_TEXT.RETURN}}</el-button>
            </div>
              <HeadOperationItem name="addDevice" @onOprationItem="Refresh()">
                <i slot="operation-icon" class="icon-refresh"></i>
                <span slot="operation-name">{{BTN_TEXT.REFRESH}}</span>
              </HeadOperationItem>
          </HeaderTitle> -->

        </el-header>
        <el-main
          v-loading="operationLoad"
          :element-loading-text="MSG.OPERATION_IN_PROGRESS"
          element-loading-spinner="el-icon-loading"
         >
          <el-radio-group v-model="tabPosition">
            <el-radio-button label="deviceInfoBasics">{{CONSTANTS_DEVICE_CONFIGURATION.BASICS_INFO}}</el-radio-button>
            <el-radio-button label="deviceInfoCollect">{{CONSTANTS_DEVICE_CONFIGURATION.COLLECT_CONFIG}}</el-radio-button>
            <el-radio-button label="deviceInfoConfig">{{CONSTANTS_DEVICE_CONFIGURATION.PARAMS_CONFIG}}</el-radio-button>
            <el-radio-button label="deviceUphold">维保信息</el-radio-button>
             <!-- <el-radio-button label="deviceInfoDebug">{{CONSTANTS_DEVICE_CONFIGURATION.DEBUG_TOOL}}</el-radio-button> -->
          </el-radio-group>
          <!-- <el-button
            @click="toggleExtend(true)"
            class="extendBtn"
            v-show="tabPosition==='deviceInfoBasics'"
          >{{CONSTANTS_DEVICE_CONFIGURATION.EXTEND}}</el-button> -->
          <div class="area-main">
            <component
            :is="currentView"
            :ref="currentView"
            @changeLoad="changeLoad"
            ></component>
          </div>
        </el-main>
      </el-container>
    </div>
    <div v-if="showExtend">
      <deviceInfoExtend @toggleExtend="toggleExtend"></deviceInfoExtend>
    </div>
  </div>
</template>

<script>
import js from '@/assets/js/main/DeviceConfiguration/deviceInfo_mk';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/DeviceConfiguration/deviceInfo.less';
