<!--
 * @Descripttion: 设备基础信息页面
 * @version:
 * @Author: ZJM
 * @Date: 2020-04-14 10:09:28
 * @LastEditors: ZJM
 * @LastEditTime: 2022-09-15 11:02:02
 -->
<template>
  <el-container class="content">
    <el-main>
      <div class="area-main-conent">

        <el-form
          ref="deviceInfo"
          :model="formInline"
          :rules="rules"
          class="demo-form-inline"
          :label-width="formLabelWidth"
          :show-message="true"
        >
          <el-row>
            <el-col :span="16">

              <el-col :span="12">
                <el-form-item prop="prCode" :label="'SAP工厂'">
                  <el-input v-model="formInline.extend01"></el-input>
                  <!-- <el-input v-model="formInline.deviceName"></el-input> -->
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item prop="prCode" :label="'SAP公司代码'">
                  <el-input v-model="formInline.extend02"></el-input>
                  <!-- <el-input v-model="formInline.deviceName"></el-input> -->
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item prop="prCode" :label="'SAP主资产号'">
                  <el-input v-model="formInline.extend03"></el-input>
                  <!-- <el-input v-model="formInline.deviceName"></el-input> -->
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item prop="prCode" :label="'SAP次及资产号'">
                  <el-input v-model="formInline.extend04"></el-input>
                  <!-- <el-input v-model="formInline.deviceName"></el-input> -->
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item prop="prCode" :label="'成本中心'">
                  <el-input v-model="formInline.extend05"></el-input>
                  <!-- <el-input v-model="formInline.deviceName"></el-input> -->
                </el-form-item>
              </el-col>

               <!-- 制造日期 -->
              <el-col :span="12">
                <el-form-item
                  prop="commissioningDate"
                  :label="CONSTANTS_DEVICE_CONFIGURATION.MANUFACTURER_DATE"
                >
                  <el-date-picker
                    type="date"
                    v-model="formInline.manufactureDate"
                    format="yyyy-MM-dd"
                    value-format="yyyy-MM-dd"
                    class="formItem_full"
                  ></el-date-picker>
                </el-form-item>
              </el-col>

              <!-- 设备名称 -->
              <el-col :span="12">
                <el-form-item prop="deviceName" :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_NAME">
                  <el-input v-model="formInline.deviceName"></el-input>
                </el-form-item>
              </el-col>

              <!-- 设备编码 -->
              <el-col :span="12">
                <el-form-item prop="prCode" :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_PRCODE">
                  <el-input v-model="formInline.devicePriCode"></el-input>
                </el-form-item>
               </el-col>

              <!-- 设备系统编码 -->
              <el-col :span="12">
                <el-form-item prop="prCode" :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_CODE">
                  <el-input disabled="true" v-model="formInline.deviceCode"></el-input>
                </el-form-item>
               </el-col>

              <!-- 设备编号 -->
              <el-col :span="12">
                <el-form-item prop="prCode" :label="CONSTANTS_DEVICE_CONFIGURATION.DEVICE_EXTEND">
                  <el-input v-model="formInline.number"></el-input>
                </el-form-item>
              </el-col>

              <!-- 设备类型 -->
              <el-col :span="12">
                  <el-form-item prop="typeId" :label="CONSTANTS_DEVICE_CONFIGURATION.TYPE">
                    <el-cascader
                      :options="typeOptions"
                      v-model="formInline.typeId"
                      :props="{ checkStrictly: true }"
                      :show-all-levels="false"
                      class="formItem_full"
                    ></el-cascader>
                  </el-form-item>
              </el-col>

              <!-- 设备描述详情 -->
              <el-col :span="12">
                <el-form-item prop="prCode" :label="'设备描述详情'">
                  <el-input v-model="formInline.details"></el-input>
                </el-form-item>
              </el-col>

              <!-- 车间分组 -->
              <el-col :span="12">
                <el-form-item prop="groupId" :label="CONSTANTS_DEVICE_CONFIGURATION.GROUP_NAME">
                  <el-cascader
                    :options="groupOptions"
                    :props="{ expandTrigger: 'hover',checkStrictly: true }"
                    v-model="formInline.groupId"
                    :show-all-levels="true"
                    class="formItem_full"
                  ></el-cascader>
                </el-form-item>
              </el-col>

              <!-- 设备等级 -->
              <el-col :span="12">
                  <el-form-item
                    :label="'设备等级'"
                  >
                    <el-select v-model="formInline.level" class="formItem_full">
                      <el-option
                        v-for="item in levelOption"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
              </el-col>

              <!-- 设备私有状态 -->
              <el-col :span="12">
                  <el-form-item
                    :label="'设备私有状态'"
                  >
                    <el-select v-model="formInline.priState" class="formItem_full">
                      <el-option
                        v-for="item in stateOption"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
              </el-col>

              <!-- 设备型号 -->
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.MODE"   prop="mode">
                  <el-input v-model="formInline.mode"></el-input>
                </el-form-item>
              </el-col>

              <!-- 地理位置 -->
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.LOCATION" prop="locationX" >
                  <el-input v-model="formInline.location"></el-input>
                </el-form-item>
              </el-col>

              <!-- 投用日期 -->
              <el-col :span="12">
                <el-form-item
                  prop="commissioningDate"
                  :label="'启用日期'"
                >
                  <el-date-picker
                    type="date"
                    v-model="formInline.commissioningDate"
                    format="yyyy-MM"
                    value-format="yyyy-MM"
                    class="formItem_full"
                  ></el-date-picker>
                </el-form-item>
              </el-col>

              <!-- 生产厂家 -->
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.MANUFACTURER" prop="manufacturer">
                  <el-input v-model="formInline.manufacturer"></el-input>
                </el-form-item>
              </el-col>

              <!-- 厂家电话 -->
              <el-col :span="12">
                <el-form-item :label="CONSTANTS_DEVICE_CONFIGURATION.MANUFACTURER_TEL" prop="manufacturerTel">
                  <el-input v-model="formInline.manufacturerTel"></el-input>
                </el-form-item>
              </el-col>

              <!-- 是否产能设备 -->
              <!-- <el-col :span="12">
                  <el-form-item
                    prop="isProductionDevice"
                    :label="'是否产能设备'"
                  >
                    <el-select v-model="formInline.isProductionDevice" class="formItem_full">
                      <el-option
                        v-for="item in deviceWarningAutoOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
              </el-col> -->

              <!-- 日计划产量 -->
              <el-col :span="12">
                <el-form-item
                  prop="dayProduction"
                  :label="CONSTANTS_DEVICE_CONFIGURATION.DAY_PRODUCTION"
                >
                  <el-input  type="number"
                    min="0"
                    max="999999" v-model="formInline.dayProduction"></el-input>
                </el-form-item>
              </el-col>

              <!-- 日计划工作时长 -->
              <el-tooltip class="item" effect="light" content="未配置无法计算时间稼动率" placement="top">
                <el-col :span="12">
                  <el-form-item
                    prop="dayWorkTime"
                    :label="CONSTANTS_DEVICE_CONFIGURATION.DAY_WORK_TIME"
                  >
                    <el-input  type="number"
                      min="0"
                      max="24" v-model="formInline.dayWorkTime"></el-input>
                  </el-form-item>
                </el-col>
              </el-tooltip>

              <!-- 平均生产周期（秒） -->
              <el-tooltip class="item" effect="light" content="未配置无法计算速度稼动率" placement="top">
                <el-col :span="12">
                  <el-form-item
                    prop="productionCycleSecond"
                    :label="'平均生产周期（秒）'"
                  >
                    <el-input  type="number"
                      min="0"
                      max="999999" v-model="formInline.productionCycleSecond"></el-input>
                  </el-form-item>
                </el-col>
              </el-tooltip>

              <!-- 最大生产周期（秒） -->
              <el-tooltip class="item" effect="light" content="未配置导致设备生产周期不可控" placement="top">
                <el-col :span="12">
                  <el-form-item
                    prop="maxProductionCycleSecond"
                    :label="'最大生产周期（秒）'"
                  >
                    <el-input  type="number"
                      min="0"
                      max="999999" v-model="formInline.maxProductionCycleSecond"></el-input>
                  </el-form-item>
                </el-col>
              </el-tooltip>

              <!-- 自动报修 -->
              <el-col :span="12">
                  <el-form-item
                    prop="deviceWarningAuto"
                    :label="CONSTANTS_DEVICE_CONFIGURATION.WARN_AUTO_SHORT"
                  >
                    <el-select v-model="formInline.deviceWarningAuto" class="formItem_full">
                      <el-option
                        v-for="item in deviceWarningAutoOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
              </el-col>

              <!-- 异常报修时长 -->
              <el-col :span="12">
                <el-form-item
                  prop="repairTimeValue"
                  :label="CONSTANTS_DEVICE_CONFIGURATION.REPAIR_TIME"
                >
                  <el-input
                    type="number"
                    min="0"
                    max="999999"
                    v-model="formInline.repairTimeValue"
                    class="formItem_time"
                  ></el-input>
                  <el-select v-model="formInline.repairTimeUnit" class="formItem_unit">
                    <el-option
                      v-for="item in repairTimeUnitOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>

              <el-col v-for="(item,index) in extendPropertyValueVoList" :key="index" :span="12">
                <el-form-item
                  :class="{'requiredStar':item.propertyIsNull===1}"
                  :label="item.extendName"
                  :label-width="formLabelWidth"
                  :prop="'extendPropertyValueVoList.'+index+'.extendValue'"
                  :rules="rules[item.propertyCode]"
                >
                  <el-input v-if="item.propertyType!==3" v-model="item.extendValue"></el-input>
                  <el-date-picker
                    v-else
                    v-model="item.extendValue"
                    type="datetime"
                    :format="FORM_CONFIG_DATE[item.propertyLength]"
                    :value-format="FORM_CONFIG_DATE[item.propertyLength]"
                  ></el-date-picker>
                </el-form-item>
              </el-col>

            </el-col>
            <el-col :span="7" class="colBorder colImage">
              <el-upload
                class="avatar-uploader"
                action="posts"
                :http-request="uploadSectionIMG"
                :show-file-list="false"
                accept="image/gif, image/jpeg, image/jpg, image/bmp, image/png"
                :on-success="handleAvatarSuccess"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <span v-else>{{CONSTANTS_DEVICE_CONFIGURATION.CLICK_UPLOAD_IMAGE}}</span>
              </el-upload>
            </el-col>
           <!--  <el-col :span="24" class="colClass">
              <el-button type="primary" @click="saveDeviceInfo">{{BTN_TEXT.SAVE}}</el-button>
              <el-button @click="reset">{{BTN_TEXT.RESET}}</el-button>
            </el-col> -->
          </el-row>
        </el-form>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/DeviceConfiguration/deviceInfoBasics_mk';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/DeviceConfiguration/deviceInfoBasics.less';
</style>
