/*
 * @Descripttion: 设备详情基础信息组件
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-22 14:52:15
 * @LastEditors: ZJM
 * @LastEditTime: 2022-09-22 10:13:14
 */
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 引入通用接口
import { deviceGroupGetTreeFunc, deviceWorkshopTypeGetTreeFunc } from '@/utils/publicFunction';
import { STATUS_CODE, TITLE, MSG, VARIABLE, BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION, FORM_CONFIG_DATE } from '@/utils/constantsFile';
// 导入日期处理类库
import moment from 'moment';
// 导入所需接口
import {
  deviceSelectDeviceByConditionAction,
  // extendPropertyGetAllAction,
  // deviceExtendPropertyValueInsertOrUpdateBatchAction,
  // deviceExtendPropertyValueGetListAction,
  basicInformationGetListByBasicType,
  deviceGroupGetTreeAction,
  deviceWorkshopTypeGetTreeAction,
  deviceUpdateBatchDeviceAction,
  fileResourceUploadOneFileAction,
  fileResourceGetByIdAction
} from '@/api/management';
import {
  // extendPropertyGetListAction
  extendPropertyValueInsertOrUpdateBatchAction
} from '@/api/extendConfig';
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem,
    moment
  },
  props: {
  },
  data() {
    var setValidateForm = (rule, value, callback) => {
      return this.$options.filters.formatterFormValidate(rule, value, callback, this.extendPropertyValueVoList);
    };

    return {
      setValidateForm: setValidateForm,
      // 常量
      FORM_CONFIG_DATE: FORM_CONFIG_DATE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      // 图片id
      imageId: 0,
      // 图片路径
      imageUrl: '',
      // 维保周期单位数据
      maintenanceUnitOptions: [
        {
          label: VARIABLE.DAY,
          value: 1
        },
        {
          label: VARIABLE.MONTH,
          value: 2
        },
        {
          label: VARIABLE.YEAR,
          value: 3
        }
      ],
      // 异常维修时长单位数据
      repairTimeUnitOptions: [{
        value: 1,
        label: VARIABLE.SECONDS
      }, {
        value: 2,
        label: VARIABLE.MINUTE
      }, {
        value: 3,
        label: VARIABLE.HOUR
      }],
      // 告警数据
      deviceWarningAutoOptions: [{
        value: 1,
        label: VARIABLE.YES
      }, {
        value: 0,
        label: VARIABLE.NO
      }],
      // 标题名称
      titleName: '',
      formLabelWidth: '150px',
      // 表单数据
      formInline: {
        deviceName: '',
        commissioningDate: '',
        devicePriCode: '',
        typeId: 0,
        groupId: 0,
        deviceWarningAuto: 0,
        repairTimeValue: 0,
        repairTimeUnit: 1,
        deviceCode: '',
        location: '',
        mode: '',
        manufacturer: '',
        manufacturerTel: '',
        dayProduction: '',
        dayWorkTime: '',
        maxProductionCycleSecond: 0,
        productionCycleSecond: 0,
        isProductionDevice: 1,
        manufactureDate: '',
        priState: null,
        level: null,
        number: '',
        details: '',
        extend01: '',
        extend02: '',
        extend03: '',
        extend04: '',
        extend05: ''
      },
      // 设备类型数据
      typeOptions: [],
      typeOptionsY: [],
      // 车间分组数据
      groupOptions: [],
      groupOptionsY: [],
      // 表单校验规则
      rules: {
      },
      // 原始校验
      OldRules: {
        deviceName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 20, message: '长度不得大于20个字符' }
        ],
        // mode: [
        //   { max: 50, message: '长度不得大于50个字符' }
        // ],
        prCode: [{ max: 30, message: '长度不得大于30个字符' }],
        typeId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT, trigger: 'change' }],
        // deviceWarningAuto: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT, trigger: 'change' }],
        groupId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT, trigger: 'change' }]
        // dayWorkTime: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT }
        // ],
        // dayWorkTime: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT }
        // ],
        // dayProduction: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT }
        // ],
        // repairTimeValue: [
        //   { required: true, message: MSG.PLEASE_ENTER_CONTENT }
        // ],
        // manufacturerTel: [{ max: 30, message: '长度不得大于30个字符' }],
        // manufacturer: [{ max: 30, message: '长度不得大于30个字符' }],
        // locationX: [{ max: 30, message: '长度不得大于30个字符' }]
      },
      definedDatas: [],
      extendPropertyValueVoList: [],
      stateOption: [],
      levelOption: []
    };
  },
  computed: {},
  created() {
    this.getDevLevel();
    this.getDevRrState();
    // 类型下拉数据
    this.getTypeTree();
    // this.setTableColume();
    // 车间分组下拉数据
    // 扩展字段数据
    // this.getDefinedData();
  },
  mounted() {

  },
  methods: {
    async getDevLevel() {
      try {
        const res = await basicInformationGetListByBasicType({
          basicInformationEnum: 'DEVICE_LEVEL'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var i = 0; i < res.rows.length; i++) {
            arr.push({
              label: res.rows[i].basicValue,
              value: res.rows[i].basicId
            });
          }
          this.levelOption = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getDevRrState() {
      try {
        const res = await basicInformationGetListByBasicType({
          basicInformationEnum: 'DEVICE_PRI_STATE'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var arr = [];
          for (var i = 0; i < res.rows.length; i++) {
            arr.push({
              label: res.rows[i].basicValue,
              value: res.rows[i].basicId
            });
          }
          this.stateOption = arr;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 上传图片
     * @param param 图片数据集
     * @return: 无
     * @author: ZJM
     */
    async uploadSectionIMG(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      fd.append('fileResourceTypeEnum', 'DEVICE_IMAGE');
      fd.append('relationshipId', this.$store.state.deviceInfoId);
      try {
        const res = await fileResourceUploadOneFileAction({}, fd);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.imageUrl = this.$store.state.publicUrl + res.rows.fileUrl;
          this.imageId = res.rows.resourceId;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    handleAvatarSuccess(res, file) {
    },
    /**
     * @description: 查询设备信息
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getDeviceInfo() {
      try {
        const res = await deviceSelectDeviceByConditionAction({
          deviceCode: this.$store.state.deviceInfoCode
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.toExtendRulesAndShow(res.rows[0]);
          this.formInline.extendPropertyValueVoList = res.rows[0].extendPropertyValueVoList;

          this.formInline.deviceName = res.rows[0].deviceName;
          if (!res.rows[0].commissioningDate) {
            this.formInline.commissioningDate = null;
          } else {
            this.formInline.commissioningDate = moment(res.rows[0].commissioningDate).format('YYYY-MM-DD');
          }
          if (!res.rows[0].manufactureDate) {
            this.formInline.manufactureDate = null;
          } else {
            this.formInline.manufactureDate = moment(res.rows[0].manufactureDate).format('YYYY-MM-DD');
          }
          if (res.rows[0].isProductionDevice === 1) {
            this.formInline.isProductionDevice = 1;
          } else {
            this.formInline.isProductionDevice = 0;
          }
          this.formInline.typeId = this.$options.filters.getCascaderIds(res.rows[0].typeId, this.typeOptionsY);
          this.formInline.groupId = this.$options.filters.getCascaderIds(res.rows[0].groupId, this.groupOptionsY);
          this.formInline.deviceWarningAuto = res.rows[0].deviceWarningAuto === undefined ? 0 : res.rows[0].deviceWarningAuto;
          this.formInline.repairTimeValue = res.rows[0].repairTimeValue === undefined ? 1 : res.rows[0].repairTimeValue;
          this.formInline.repairTimeUnit = res.rows[0].repairTimeUnit === undefined ? 1 : res.rows[0].repairTimeUnit;
          this.formInline.devicePriCode = res.rows[0].devicePriCode;
          this.formInline.location = res.rows[0].location;
          this.formInline.mode = res.rows[0].mode;
          this.formInline.manufacturer = res.rows[0].manufacturer;
          this.formInline.manufacturerTel = res.rows[0].manufacturerTel;
          this.formInline.dayProduction = res.rows[0].dayProduction;
          this.formInline.dayWorkTime = res.rows[0].dayWorkTime;
          this.formInline.maxProductionCycleSecond = res.rows[0].maxProductionCycleSecond;
          this.formInline.productionCycleSecond = res.rows[0].productionCycleSecond;
          this.formInline.priState = res.rows[0].priState;
          this.formInline.level = res.rows[0].level;
          this.formInline.details = res.rows[0].details;
          this.formInline.extend01 = res.rows[0].extend01;
          this.formInline.extend02 = res.rows[0].extend02;
          this.formInline.extend03 = res.rows[0].extend03;
          this.formInline.extend04 = res.rows[0].extend04;
          this.formInline.extend05 = res.rows[0].extend05;
          this.formInline.number = res.rows[0].number;
          this.formInline.deviceCode = res.rows[0].deviceCode;
          // if (!res.rows[0].maintenanceTime) {
          //   this.formInline.maintenanceTime = null;
          // } else {
          //   this.formInline.maintenanceTime = moment(res.rows[0].maintenanceTime).format('YYYY-MM-DD');
          // }
          // this.formInline.maintenanceCycle = res.rows[0].maintenanceCycle;
          // this.formInline.maintenanceCycleTimeType = res.rows[0].maintenanceCycleTimeType;
          if (res.rows[0].imageFileId) {
            this.imageId = res.rows[0].imageFileId;
            this.getDeviceImageUrl();
          }
          this.$refs.deviceInfo.clearValidate();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询设备图片
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    async getDeviceImageUrl() {
      try {
        const res = await fileResourceGetByIdAction({
          id: this.imageId
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.imageUrl = this.$store.state.publicUrl + res.rows.fileUrl;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 保存设备采集信息验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    saveDeviceInfo() {
      this.$refs.deviceInfo.validate(valid => {
        // 若验证通过
        if (valid) {
          this.setSaveDeviceInfo();
          // this.setExtend();
        }
      });
    },
    /**
    * @description: 设备保存采集信息
    * @param 无
    * @return: 无
    * @author: ZJM
    */
    async setSaveDeviceInfo() {
      try {
        const form = JSON.parse(JSON.stringify(this.formInline));
        delete form.deviceCode;
        // delete form.extendPropertyValueVoList;
        form.deviceId = this.$store.state.deviceInfoId;
        form.deviceCodeList = [this.$store.state.deviceInfoCode];
        if (this.formInline.commissioningDate) {
          if (this.formInline.commissioningDate.length > 7) {
            form.commissioningDate = this.formInline.commissioningDate + ' 00:00:00';
          } else {
            form.commissioningDate = this.formInline.commissioningDate + '-01' + ' 00:00:00';
          }
        } else {
          delete form.commissioningDate;
        }
        // form.maintenanceTime = this.formInline.maintenanceTime + ' 00:00:00';
        form.typeId = this.formInline.typeId[form.typeId.length - 1];
        form.groupId = this.formInline.groupId[form.groupId.length - 1];
        if (this.imageId) {
          form.imageFileId = this.imageId;
        }
        const res = await deviceUpdateBatchDeviceAction(form);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 提示正常信息
          showSuccessMessage(res.message);
          if (this.extendPropertyValueVoList.length > 0) {
            this.saveExtendValue();
          }
          // this.setDeviceType();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      try {
        const res = await deviceGroupGetTreeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptionsY = res.rows;
          this.getDeviceInfo();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取设备类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getTypeTree() {
      // 若是设备类型
      // 执行调用接口
      try {
        const res = await deviceWorkshopTypeGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.typeOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      try {
        const res = await deviceWorkshopTypeGetTreeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.typeOptionsY = res.rows;
          this.getGroupTree();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 表单中扩展字段处理
     * @return: void
     * @author: ZJM
     */
    toExtendRulesAndShow(row) {
      // 保存扩展字段数据
      var arr = row.extendPropertyValueVoList.map((item) => {
        item.relationshipTypeId = this.$store.state.deviceInfoId;
        return item;
      });
      this.extendPropertyValueVoList = arr;
      this.rules = JSON.parse(JSON.stringify(this.OldRules));
      for (var item in this.extendPropertyValueVoList) {
        // 若类型是日期
        if (this.extendPropertyValueVoList[item].propertyType === 3) {
          this.rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: ['blur', 'change'] }
          ];
        } else { // 若类型是字符或数字
          this.rules[this.extendPropertyValueVoList[item].propertyCode] = [
            { validator: this.setValidateForm, trigger: 'blur' }
          ];
        }
      }
    },
    /**
     * @description: 保存扩展字段数据
     * @return: void
     * @author: ZJM
     */
    async saveExtendValue() {
      var arr = this.extendPropertyValueVoList.map((item) => {
        if (item.extendValue === null) {
          item.extendValue = '';
        }
        return item;
      });

      try {
        const res = await extendPropertyValueInsertOrUpdateBatchAction({}, arr);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.findTableList();
          this.getDeviceInfo();
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    reset() {
      this.imageId = 0;
      this.imageUrl = '';
      this.$refs.deviceInfo.resetFields();
      // this.getDefinedData();
      this.getDeviceInfo();
    }
  }
};
