import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 导入所需接口
import {
  deviceLedgerGetDeviceLedgerByDeviceCode
} from '@/api/management';
import {
  STATUS_CODE,
  TITLE, VARIABLE,
  BTN_TEXT, CONSTANTS_DEVICE_CONFIGURATION,
  EQUIPMENT_DATA,
  MSG,
  ENERGY,
  PAGE_TITLE,
  FIXTASK_STATUS,
  FIXTASK_TYPE_STATUS,
  EXPORT_NAME,
  REPAIR_TASK,
  MAINTAINTASK_STATUS
} from '@/utils/constantsFile';
// 引入标题组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 引入标题元素组件
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 导入所需接口
export default {
  components: {
    // 加载标题组件
    HeaderTitle,
    HeadOperationItem
  },
  props: {
  },
  data() {
    return {
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      REPAIR_TASK: REPAIR_TASK,
      VARIABLE: VARIABLE,
      EXPORT_NAME: EXPORT_NAME,
      FIXTASK_TYPE_STATUS: FIXTASK_TYPE_STATUS,
      TITLE: TITLE,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      MSG: MSG,
      FIXTASK_STATUS: FIXTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_DEVICE_CONFIGURATION: CONSTANTS_DEVICE_CONFIGURATION,
      // 需要执行几个方法
      funcIndex: 0,
      // 执行了几个方法
      funcDo: 0,
      // 标题名称
      titleName: '',
      // 旧数据
      oldRow: {},
      // 可选参数表头配置
      tableColumnsRepair: [
        {
          type: 'seq',
          title: VARIABLE.SEQ,
          align: 'center',
          width: '60'
        },
        {
          field: 'repairCode',
          visible: true,
          title: ENERGY.REPAIR_CODE,
          align: 'center'
        },
        {
          field: 'deviceName',
          visible: true,
          title: ENERGY.DEVICENAME,
          align: 'center'
        },
        {
          field: 'devicePriCode',
          visible: true,
          title: ENERGY.DEVICECODE,
          align: 'center'
        },
        {
          field: 'repairTypeNameList',
          visible: true,
          title: EQUIPMENT_DATA.REPAIR_TYPENAMELIST,
          align: 'center'
        },
        {
          field: 'occurTime',
          visible: true,
          title: EQUIPMENT_DATA.OCCURTIME,
          align: 'center'
        },
        {
          field: 'warrantyDescription',
          visible: true,
          title: '报修描述',
          align: 'center'
        },
        {
          field: 'totalTime',
          visible: true,
          title: EQUIPMENT_DATA.REPAIR_TOTALTIME,
          align: 'center',
          formatter: this.$options.filters.formatDifferentTime
        },
        {
          field: 'users',
          visible: true,
          title: EQUIPMENT_DATA.REPAIR_USER,
          align: 'center'
        },
        {
          field: 'repairState',
          visible: true,
          title: EQUIPMENT_DATA.STATUS,
          formatter: ({ row }) => {
            return FIXTASK_STATUS[row.repairState];
          },
          align: 'center',
          slots: {
            default: ({ row }) => {
              return FIXTASK_STATUS[row.repairState];
            }
          }
        }
      ],
      // 自定义参数表头配置
      tableColumnsMaintain: [
        {
          type: 'seq',
          title: VARIABLE.SEQ,
          align: 'center',
          width: '60'
        },
        {
          field: 'maintainCode',
          visible: true,
          title: ENERGY.MAINTANCE_CODE,
          align: 'center'
        },
        {
          field: 'deviceName',
          visible: true,
          title: ENERGY.DEVICENAME,
          align: 'center'
        },
        {
          field: 'devicePriCode',
          visible: true,
          title: ENERGY.DEVICECODE,
          align: 'center'
        },
        {
          field: 'typeName',
          visible: true,
          title: PAGE_TITLE.DEVICE_TYPE,
          align: 'center'
        },
        {
          field: 'mainLastTime',
          visible: true,
          title: EQUIPMENT_DATA.LAST_MAINTANCE_TIME,
          align: 'center'
        },
        {
          field: 'maintainTime',
          visible: true,
          title: EQUIPMENT_DATA.MAINTANCE_TIME_PLAN,
          align: 'center'
        },
        {
          field: 'levelName',
          visible: true,
          title: EQUIPMENT_DATA.MAINTANCE_LEVEL,
          align: 'center'
        },
        {
          field: 'maintainState',
          title: EQUIPMENT_DATA.STATUS,
          visible: true,
          align: 'center',
          slots: {
            default: ({ row }) => {
              return [<span>{MAINTAINTASK_STATUS[row.maintainState]}</span>];
            }
          }
        }
      ],
      // 告警参数表头配置
      tableColumnsCheck: [
        {
          type: 'seq',
          title: VARIABLE.SEQ,
          align: 'center',
          width: '60'
        },
        {
          field: 'dayDateTime',
          title: '日期',
          align: 'center',
          formatter: ({ cellValue }) => {
            return cellValue.substring(0, 10);
          }
        },
        {
          field: 'shiftUserCheckVoList',
          title: '点检信息',
          align: 'center',
          formatter: ({ cellValue }) => {
            console.log(cellValue);
            if (cellValue.length === 0) {
              return '无点检内容';
            } else {
              var arr = [];
              for (var i = 0; i < cellValue.length; i++) {
                var html = '';
                html += cellValue[i].shiftName;
                html += ' ';
                html += cellValue[i].shiftInformation.shiftStartTime;
                html += ' - ';
                html += cellValue[i].shiftInformation.shiftEndTime;
                html += ' ';
                if (cellValue[i].realNameList.length === 0) {
                  html += '未点检';
                } else {
                  html += cellValue[i].realNameList[0];
                }
                arr.push(html);
              }
              return arr.join(' , ');
            }
          }
        }
      ],
      // 可选参数数据
      tableDatasRepair: [],
      // 自定义参数数据
      tableDatasMaintain: [],
      // 告警参数数据
      tableDatasCheck: [],
      dateTimes: [
        this.$utils.toDateString(
          new Date() - 60 * 60 * 24 * 1000 * 7,
          'yyyy-MM-dd'
        ),
        this.$utils.toDateString(new Date(), 'yyyy-MM-dd')
      ],
      pickerOptionsx: {
        // disabledDate(time) {
        //   return time.getTime() > Date.now();
        // },
        onPick: (time) => {
          // 当第一时间选中才设置禁用
          if (time.minDate && !time.maxDate) {
            this.timeOptionRangex = time.minDate;
          }
          if (time.maxDate) {
            this.timeOptionRangex = null;
          }
        },
        timeOptionRangex: ''
        // disabledDate: (time) => {
        //   var timeOptionRangex = this.timeOptionRangex;
        //   var secondNum = 60 * 60 * 24 * 30 * 1000;
        //   if (timeOptionRangex) {
        //     if (timeOptionRangex.getTime() + secondNum > Date.now() - 8.64e6) {
        //       return (
        //         time.getTime() > Date.now() - 8.64e6 ||
        //         time.getTime() < timeOptionRangex.getTime() - secondNum
        //       );
        //     }
        //     return (
        //       time.getTime() > timeOptionRangex.getTime() + secondNum ||
        //       time.getTime() < timeOptionRangex.getTime() - secondNum
        //     );
        //   } else {
        //     return time.getTime() > Date.now();
        //   }
        // }
      }
    };
  },
  computed: {},
  created() {
    // 获取可选参数
    // this.getCanSelectData();
    // 获取自定义参数
    // this.getDefinedData();
    this.getUphold();
  },
  mounted() {
  },
  watch: {
    dateTimes(val) {
      this.dateTimes = val;
      this.getUphold();
    }
  },
  methods: {
    async getUphold() {
      try {
        const res = await deviceLedgerGetDeviceLedgerByDeviceCode({
          deviceCode: this.$store.state.deviceInfoCode,
          endTime: this.dateTimes[1] + ' 23:59:59',
          startTime: this.dateTimes[0] + ' 00:00:00'
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log(res);
          this.tableDatasRepair = res.rows.repairTaskVoList;
          this.tableDatasMaintain = res.rows.maintainTaskVoList;
          this.tableDatasCheck = res.rows.checkListHistoryVoList;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    exportTable(tableRef) {
      var columns = false;
      var fileName = '';
      var data = [];

      if (tableRef === 'repairTable') {
        fileName = '维修任务记录';
        data = this.tableDatasRepair;
      } else if (tableRef === 'maintainTable') {
        fileName = '保养任务记录';
        data = this.tableDatasMaintain;
      } else if (tableRef === 'checkTable') {
        fileName = '保养任务记录';
        data = this.tableDatasCheck;
      }

      const option = {
        filename: fileName,
        type: 'csv',
        isHeader: true,
        isFooter: true,
        data: data
      };
      if (columns) {
        option.columnFilterMethod = (column) =>
          columns.includes(column.property);
      }
      this.$refs[tableRef].exportData(option);
    }
  }
};
