 <template>
  <el-container class="content">
    <el-main>
        <div class="area-main-conent">
          <el-header>
            <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.CAN_CHOOSE_PARAMS"></HeaderTitle>
          </el-header>
          <div class="area-main-conent">
            <vxe-grid
              border="full"
              resizable
              keep-source
              ref="canSelect"
              class="vxe-table-element"
              height="600"
              :columns="tableColumnsCanSelect"
              :data="tableDatasCanSelect"
              :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o'}"
              @edit-actived="editActivedCanSelectEvent"
              @edit-closed="editClosedCanSelectEvent"
            ></vxe-grid>
          </div>
        </div>
        <div class="area-main-conent">
          <el-header>
            <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.WARN_PARAMS"></HeaderTitle>
          </el-header>
          <div class="area-main-conent">
            <vxe-grid
              border="full"
              resizable
              keep-source
              ref="warning"
              class="vxe-table-element"
              height="300"
              :columns="tableColumnsWarning"
              :data="tableDatasWarning"
              :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o'}"
            ></vxe-grid>
          </div>
        </div>
        <div class="area-main-conent">
          <el-header>
            <HeaderTitle class="header-title" :titleName="CONSTANTS_DEVICE_CONFIGURATION.DEFINED_PARAMS">
              <div slot="head-operation" class="head-operation">
              <HeadOperationItem
                name="addParams"
                @onOprationItem="insertEvent"
              >
                <i slot="operation-icon" class="icon-add"></i>
                <span slot="operation-name">{{BTN_TEXT.ADD}}</span>
              </HeadOperationItem>
            </div>
            </HeaderTitle>
          </el-header>
          <div class="area-main-conent">
            <vxe-grid
              border="full"
              resizable
              keep-source
              ref="defined"
              class="vxe-table-element"
              height="300"
              :edit-rules="validRulesDefined"
              :columns="tableColumnsDefined"
              :data="tableDatasDefined"
              :edit-config="{trigger: 'click', mode: 'cell', showStatus: true, icon: 'fa fa-file-text-o'}"
              @edit-actived="editActivedDefinedEvent"
            ></vxe-grid>
          </div>
        </div>
        <!-- <div class="area-main-conent">
          <el-button type="primary" @click="fullValidEvent" >{{BTN_TEXT.SAVE}}</el-button>
          <el-button  @click="reset">{{BTN_TEXT.RESET}}</el-button>
        </div> -->
    </el-main>
  </el-container>
</template>

<script>
import js from '@/assets/js/main/DeviceConfiguration/deviceInfoConfig';
export default js;
</script>

<style lang="less" scoped>
@import '../../../assets/less/main/DeviceConfiguration/deviceInfoConfig.less';
</style>
